import React, { useEffect } from 'react';
import { Button, Input } from 'reactstrap';

export const Rep = ({ item, onItemChange }) => {
  useEffect(() => {
    Object.keys(item).map(key => console.log(key + ' -> ' + item[key].value));
  }, []);
  if (!item.id) console.log("Data item must contain 'id' field");
  if (!item.editable) return <></>;

  return (
    <tr>
      {Object.keys(item).map(key => {
        console.log(key);
        console.log(item[key]);
        if (key === 'id') {
          return <th scope="row">{item[key].value}</th>;
        }
        if (key === 'editable') {
          return (
            <td>
              <Button
                color={item[key].value ? 'primary' : 'danger'}
                onClick={event => onItemChange({ ...item, editable: { value: !item[key].value } })}
              >
                {item[key].value ? 'edit' : 'done'}
              </Button>
            </td>
          );
        } else {
          return (
            <td>
              {!!item.editable.value ? (
                <Input
                  type={item[key].type}
                  value={item[key].value}
                  onChange={event =>
                    onItemChange({
                      ...item,
                      key: { value: event.target.value, type: item[key].type },
                    })
                  }
                />
              ) : (
                item[key].value
              )}
            </td>
          );
        }
      })}
    </tr>
  );
};
